.result{
    height: 100vh;
    display: grid;
    place-items: center;
}

.result__box{
    padding: 10px;
    text-align: center;
    background-color: #64e10e;
    border-radius: 10px;
    box-shadow: 0 5px 6px rgba(0, 0, 0, 0.12), 0 2px 3px;
    padding-top: 50px;
    padding-bottom: 50px;
}